#videoPage {
  overflow: hidden;
  width: 100%;
  position: relative;
  padding-bottom: 80px;
}
#videoPage .hide {
  display: none;
}
#videoPage .headBox {
  padding-right: 88px;
  box-sizing: border-box;
  height: 88px;
  position: relative;
  background-color: #fff;
}
#videoPage .headBox .head {
  overflow-x: scroll;
  overflow-y: hidden;
  height: 88px;
  position: relative;
}
#videoPage .headBox .head::-webkit-scrollbar {
  display: none;
}
#videoPage .headBox .head .columName div {
  min-width: 91px;
  padding: 9px 15px;
  box-sizing: border-box;
  color: #78787A;
  font-size: 16px;
  text-align: center;
  border-radius: 2px;
  border: 1px solid #999999;
  display: inline-block;
  margin-right: 16px;
  cursor: pointer;
}
#videoPage .headBox .head .columName .link-active {
  color: #E94D42;
  border-radius: 2px;
  border: 1px solid #e94d42;
}
#videoPage .headBox .columName {
  white-space: nowrap;
  display: inline-table;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
}
#videoPage .headBox .edit {
  width: 88px;
  height: 88px;
  line-height: 88px;
  text-align: center;
  position: absolute;
  right: 0;
  top: 0;
  cursor: pointer;
  background-color: #fff;
}
#videoPage .content {
  padding-right: 1.5%;
  box-sizing: border-box;
}
#videoPage .content .secondType .secondList {
  display: inline-block;
  background: #f4f4f4;
  border-radius: 6px;
  padding: 8px 18px;
  color: #999999;
  font-size: 16px;
  cursor: pointer;
  margin-right: 30px;
}
#videoPage .content .secondType .second-active {
  background-color: #F06E65;
  color: #fff;
}
#videoPage .content .columnEdit,
#videoPage .content .articleEdit {
  padding: 26px 0;
  font-size: 26px;
  font-weight: 500;
  color: #333333;
  letter-spacing: 1px;
}
#videoPage .content .columnEdit img,
#videoPage .content .articleEdit img {
  width: 24px;
  vertical-align: middle;
  margin-top: -0.4%;
  margin-right: 8px;
}
#videoPage .content .columnEditBox,
#videoPage .content .articleEditBox {
  background: #eceff4;
  padding: 8px 20px;
}
#videoPage .content .columnEditBox span,
#videoPage .content .articleEditBox span {
  width: 83px;
  background: #ffffff;
  border: 1px solid #c3c5ce;
  text-align: center;
  padding: 6px 8px;
  display: inline-block;
  font-size: 16px;
  color: #333333;
  cursor: pointer;
  margin-right: 16px;
}
#videoPage .content .columnEditBox .status-active,
#videoPage .content .articleEditBox .status-active {
  background: #f06e65;
  border: 1px solid #e94035;
  color: #fff;
  margin-right: 16px;
}
#videoPage .content .columnEditList {
  padding: 20px 0;
}
#videoPage .content .columnEditList .noData {
  text-align: center;
  margin-top: 5%;
}
#videoPage .content .columnEditList .list {
  display: flex;
  align-items: center;
  border-bottom: 1px solid #f0f0f0;
  padding: 12px 0;
}
#videoPage .content .columnEditList .list img {
  width: 28px;
  border-radius: 50%;
  margin-right: 20px;
}
#videoPage .content .columnEditList .list .name {
  padding-right: 10%;
}
#videoPage .content .columnEditList .list .dept {
  flex: 1;
}
#videoPage .content .columnEditList .list span {
  color: #666666;
  font-size: 16px;
}
#videoPage .content .videoList {
  width: 100%;
}
#videoPage .content .videoList .list {
  padding: 20px 25% 14px 1.5%;
  width: 100%;
  box-sizing: border-box;
  position: relative;
  overflow: hidden;
  background-color: #F9FBFF;
  margin-bottom: 8px;
}
#videoPage .content .videoList .list .flexBox {
  display: flex;
  align-items: center;
}
#videoPage .content .videoList .list .flexBox .flex {
  flex: 1;
  overflow: hidden;
  padding-left: 2%;
  box-sizing: border-box;
}
#videoPage .content .videoList .list .flexBox .flex_l {
  width: 120px;
  height: 80px;
  position: relative;
  overflow: hidden;
  border-radius: 8px;
}
#videoPage .content .videoList .list .flexBox .flex_l img {
  width: 100%;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}
#videoPage .content .videoList .list .title {
  max-width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  color: #1E2226;
  font-size: 20px;
  font-weight: 600;
}
#videoPage .content .videoList .list .topImg {
  width: 7%;
  position: absolute;
  right: 17%;
  top: 20%;
}
#videoPage .content .videoList .list .list_foot {
  margin-top: 2.7%;
  align-items: flex-end;
}
#videoPage .content .videoList .list .list_foot div {
  margin-right: 10%;
  color: #999999;
}
#videoPage .content .videoList .list .list_foot div i {
  margin-right: 10px;
}
#videoPage .content .videoList .list .isTop {
  width: 49px;
  height: 24px;
  border: 1px solid #c3c5ce;
  position: absolute;
  right: 1.2%;
  top: 20%;
  font-size: 14px;
  line-height: 24px;
  font-weight: 500;
  color: #666666;
  text-align: center;
  cursor: pointer;
}
#videoPage .content .videoList .list .cancelTop {
  width: 76px;
  height: 24px;
  border: 1px solid #f9aba6;
  position: absolute;
  right: 1.2%;
  top: 20%;
  font-size: 14px;
  line-height: 24px;
  font-weight: 500;
  color: #eea49f;
  text-align: center;
  cursor: pointer;
}
#videoPage .content .videoList .list .operation {
  position: absolute;
  bottom: 2%;
  right: 2.5%;
  background-color: #F1948E;
  width: 7%;
  font-size: 1em;
  text-align: center;
  line-height: 34px;
  color: #fff;
  border-radius: 6px;
  cursor: pointer;
}
#videoPage .content .pagination {
  display: flex;
  padding: 10px 0;
  align-content: center;
  justify-content: center;
  position: absolute;
  bottom: 20px;
  width: 100%;
  left: 0;
}
#videoPage .content .pagination span {
  display: inline-block;
  padding: 8px;
  cursor: pointer;
}
#videoPage .content .flexBox {
  display: flex;
}
#videoPage .content .flexBox .flex {
  flex: 1;
}
.editColumn .ant-modal-body {
  padding: 32px 85px;
  box-sizing: border-box;
}
.editColumn .ant-modal-body .ant-col-12 {
  width: 70%;
}
.editColumn .ant-form-item-label > label {
  color: #333333;
}
.editColumn .flexBox {
  display: flex;
}
.editColumn .flexBox .flex {
  flex: 1;
}
.editColumn .flexBox .ant-col-5 {
  display: inline;
}
.editColumn .flexBox .ant-col-12 {
  width: 60%;
}
.editColumn .flexBox .ant-form-item-label {
  line-height: 20px;
}
.editColumn .flexBox {
  color: #333333;
}
.editColumn .flexBox .ant-col-5 span {
  display: inline-block;
  margin-left: 2px;
}
.editColumn .flexBox .flex {
  text-align: center;
  justify-content: center;
}
.editColumn .flexBox .flex span {
  height: 21px;
  display: inline-block;
  line-height: 21px;
  cursor: pointer;
  color: #333333;
}
.editColumn .flexBox .flex span span {
  border: 1px solid #979797;
  width: 21px;
  height: 21px;
  border-radius: 50%;
  text-align: center;
  display: inline-block;
  margin-right: 8px;
  vertical-align: middle;
  margin-top: -10.8%;
}
.editColumn .flexBox .flex span .active {
  background: #f1948e;
  border-color: #f1948e;
}
.editColumn .btn {
  padding-top: 60px;
  text-align: center;
}
.editColumn .btn span {
  display: inline-block;
  cursor: pointer;
  width: 112px;
  height: 48px;
  line-height: 48px;
  font-size: 20px;
  border-radius: 2px;
  border: 1px solid #979797;
  margin-left: 30px;
}
.editColumn .btn .active {
  background: #f06e65;
  border-radius: 2px;
  border: 1px solid #ee6258;
  color: #fff;
}
.adjustColumn .ant-modal-header {
  background-color: #F1F1F1;
  border: none;
}
.adjustColumn input[type='radio'],
.adjustColumn input[type='checkbox'],
.adjustColumn .ant-checkbox-inner {
  border-radius: 50%;
}
.adjustColumn .ant-checkbox-checked .ant-checkbox-inner {
  background-color: #F1948E;
  border: 1px solid #F1948E;
}
.adjustColumn .ant-checkbox-inner::after {
  left: 28%;
}
.adjustColumn .ant-modal-title {
  text-align: center;
}
.adjustColumn .ant-tree-child-tree .ant-tree-treenode-switcher-open {
  background-color: #F1948E;
}
.adjustColumn .ant-modal-body {
  padding: 32px 52px;
}
.adjustColumn .ant-modal-body li {
  list-style: none;
}
.adjustColumn .ant-modal-body .container {
  display: flex;
}
.adjustColumn .ant-modal-body .container .ant-tree-iconEle img {
  width: 20px;
  margin-right: 6px;
  vertical-align: middle;
  margin-top: -17.2%;
}
.adjustColumn .ant-modal-body .container .ant-tree-child-tree img {
  width: 16px;
}
.adjustColumn .ant-modal-body .container .flex {
  flex: 1;
}
.adjustColumn .ant-modal-body .container .title {
  padding-bottom: 30px;
  font-size: 22px;
  color: #333;
}
.adjustColumn .ant-modal-body .container .select .tips {
  font-size: 20px;
  font-weight: 500;
  color: #f1948e;
  padding: 16px 34px;
  box-sizing: border-box;
}
.adjustColumn .ant-modal-body .container .content {
  background: #f4f6f8;
  border-radius: 4px;
  padding: 16px 0px;
  box-sizing: border-box;
  border: 1px solid #cdcdcd;
  overflow-y: scroll;
  height: 510px;
}
.adjustColumn .ant-modal-body .container .search {
  text-align: center;
}
.adjustColumn .ant-modal-body .container .selected {
  margin-left: 20px;
}
.adjustColumn .ant-modal-body .container .selected .content {
  padding: 16px;
}
.adjustColumn .ant-modal-body .container .selected .content .ant-checkbox-group > div {
  padding: 6px 0;
}
.adjustColumn .ant-modal-body .container .selected .selectedList {
  position: relative;
}
.adjustColumn .ant-modal-body .deptList {
  padding: 10px 0;
}
.adjustColumn .ant-modal-body .deptList .firstDrpt .head {
  background-color: #fff;
  padding: 8px 0;
  padding-left: 10px;
  cursor: pointer;
  box-sizing: border-box;
  color: #666;
  font-size: 16px;
}
.adjustColumn .ant-modal-body .deptList .firstDrpt .head img {
  width: 20px;
  margin: 0 3px;
}
.adjustColumn .ant-modal-body .ant-collapse-item-active > .ant-collapse-header {
  background-color: #fff;
}
.adjustColumn .ant-modal-body .ant-collapse-borderless > .ant-collapse-item {
  border: none;
}
.adjustColumn .ant-modal-body .ant-collapse-borderless {
  background-color: #f4f6f8;
}
.adjustColumn .ant-modal-body .ant-collapse-header img {
  width: 20px;
  margin-right: 6px;
}
.adjustColumn .ant-modal-body .ant-collapse-content-box {
  padding: 0;
  padding-left: 20px;
  box-sizing: border-box;
}
.adjustColumn .ant-modal-body .ant-collapse-content-box > div > span {
  width: 100%;
  padding: 10px 0;
  display: inline-block;
  padding-left: 20px;
  box-sizing: border-box;
  cursor: pointer;
}
.adjustColumn .ant-modal-body .btn {
  padding-top: 30px;
  text-align: center;
}
.adjustColumn .ant-modal-body .btn div {
  display: inline-block;
  width: 112px;
  height: 48px;
  text-align: center;
  line-height: 48px;
  font-size: 20px;
  cursor: pointer;
}
.adjustColumn .ant-modal-body .btn .send {
  background: #f06e65;
  border-radius: 2px;
  border: 1px solid #ee6258;
  color: #fff;
}
.adjustColumn .ant-modal-body .btn .close {
  border-radius: 2px;
  border: 1px solid #979797;
  margin-left: 42px;
}
